<template>
	<div class="main-content">

		<!--  -->
		<div v-if="showFlag">
			<el-form :inline="true" :model="searchForm" class="form-content">
				<el-row :gutter="20" class="slt"
					:style="{justifyContent:contents.searchBoxPosition=='1'?'flex-start':contents.searchBoxPosition=='2'?'center':'flex-end'}">


					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.yonghuName" placeholder=""
							clearable></el-input>
					</el-form-item>


					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.zuoyeUuidNumber" placeholder=""
							clearable></el-input>
					</el-form-item>

					<el-form-item :label="contents.inputTitle == 1 ? '' : ''">
						<el-input prefix-icon="el-icon-search" v-model="searchForm.zuoyeName" placeholder=""
							clearable></el-input>
					</el-form-item>


					<el-form-item>
						<el-button type="success" @click="search()"><i class="el-icon-search el-icon--right" />
						</el-button>
					</el-form-item>
				</el-row>
				<el-row class="ad"
					:style="{justifyContent:contents.btnAdAllBoxPosition=='1'?'flex-start':contents.btnAdAllBoxPosition=='2'?'center':'flex-end'}">
					<el-form-item>
						<el-button v-if="isAuth('huida','')" type="success" icon="el-icon-plus"
							@click="addOrUpdateHandler()"></el-button>
						&nbsp;
						<el-button v-if="isAuth('huida','')" :disabled="dataListSelections.length <= 0" type="danger"
							icon="el-icon-delete" @click="deleteHandler()"></el-button>
						&nbsp;
						<el-button v-if="isAuth('huida','')" type="success" icon="el-icon-pie-chart"
							@click="chartDialog()"></el-button>
						&nbsp;
						<!-- excel -->
						<download-excel v-if="isAuth('huida','')" style="display: inline-block"
							class="export-excel-wrapper" :data="dataList" :fields="json_fields" name="huida.xls">
							<!-- excel -->
							<el-button type="success" icon="el-icon-download"></el-button>
						</download-excel>
						&nbsp;
					</el-form-item>
				</el-row>
			</el-form>
			<div class="table-content">
				<el-table class="tables" :size="contents.tableSize" :show-header="contents.tableShowHeader"
					:header-row-style="headerRowStyle" :header-cell-style="headerCellStyle"
					:border="contents.tableBorder" :fit="contents.tableFit" :stripe="contents.tableStripe"
					:row-style="rowStyle" :cell-style="cellStyle"
					:style="{width: '100%',fontSize:contents.tableContentFontSize,color:contents.tableContentFontColor}"
					v-if="isAuth('huida','')" :data="dataList" v-loading="dataListLoading"
					@selection-change="selectionChangeHandler">
					<el-table-column v-if="contents.tableSelection" type="selection" header-align="center"
						align="center" width="50">
					</el-table-column>
					<el-table-column label="" v-if="contents.tableIndex" type="index" width="50" />
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="yonghuName"
						header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.yonghuName}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="yonghuPhone"
						header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.yonghuPhone}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="yonghuPhoto"
						header-align="center" width="200" label="">
						<template slot-scope="scope">
							<div v-if="scope.row.yonghuPhoto">
								<img :src="scope.row.yonghuPhoto" width="100" height="100">
							</div>
							<div v-else></div>
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign"
						prop="zuoyeUuidNumber" header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.zuoyeUuidNumber}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="zuoyeName"
						header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.zuoyeName}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="zuoyeTypes"
						header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.zuoyeValue}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="huifuFile"
						header-align="center" width="200" label="">
						<template slot-scope="scope">
							<div v-if="scope.row.huifuFile">
								<a type="text" style="text-decoration:none" class="el-button"
									:href="scope.row.huifuFile"></a>
							</div>
							<div v-else></div>
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="huifuChengji"
						header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.huifuChengji}}
						</template>
					</el-table-column>
					<el-table-column :sortable="contents.tableSortable" :align="contents.tableAlign" prop="huidaTypes"
						header-align="center" label="">
						<template slot-scope="scope">
							{{scope.row.huidaValue}}
						</template>
					</el-table-column>

					<el-table-column width="300" :align="contents.tableAlign" header-align="center" label="">
						<template slot-scope="scope">
							<el-button v-if="isAuth('huida','')" type="success" icon="el-icon-tickets" size="mini"
								@click="addOrUpdateHandler(scope.row.id,'info')"></el-button>
							<el-button v-if="isAuth('huida','') && scope.row.huidaTypes == 1" type="primary" icon="el-icon-edit" size="mini"
								@click="pigaizuoye(scope.row.id)"></el-button>
							<el-button v-if="isAuth('huida','')" type="danger" icon="el-icon-delete" size="mini"
								@click="deleteHandler(scope.row.id)"></el-button>

						</template>
					</el-table-column>
				</el-table>
				<el-pagination clsss="pages" :layout="layouts" @size-change="sizeChangeHandle"
					@current-change="currentChangeHandle" :current-page="pageIndex" :page-sizes="[10, 20, 50, 100]"
					:page-size="Number(contents.pageEachNum)" :total="totalPage" :small="contents.pageStyle"
					class="pagination-content" :background="contents.pageBtnBG"
					:style="{textAlign:contents.pagePosition==1?'left':contents.pagePosition==2?'center':'right'}">
				</el-pagination>
			</div>
		</div>
		<!-- /  search-->
		<add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>


		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="addshuju">
				<el-form-item label="">
					<el-input v-model="addshuju.huifuChengji" type="number" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false"> </el-button>
				<el-button type="primary" @click="pigai"> </el-button>
			</div>
		</el-dialog>


		<el-dialog title="" :visible.sync="chartVisiable" width="800">
			<el-date-picker v-model="echartsDate" type="year" placeholder="">
			</el-date-picker>
			<el-button @click="chartDialog()"></el-button>
			<div id="statistic" style="width:100%;height:600px;"></div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="chartVisiable = false"></el-button>
			</span>
		</el-dialog>

	</div>
</template>
<script>
	import AddOrUpdate from "./add-or-update";
	import styleJs from "../../../utils/style.js";
	import utilsJs from "../../../utils/utils.js";

	export default {
		data() {
			return {
