					'': 'yonghuIdNumber',
					'': 'yonghuPhoto',
					'': 'banjiValue',
					'': 'yonghuEmail',
					'': 'banjiValue',
					'': 'zuoyeUuidNumber',
					'': 'zuoyeName',
					'': 'zuoyeValue',
					'': 'zuoyeFile',
					//
					'': "huifuFile",
					'': "huifuChengji",
					'': "huidaValue",
				},

			};
		},
